.output {
    grid-column: 1 / -1;
    background-color: rgba(0, 0, 0, 0.75);
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-around;
    padding: 0.75vw;
    word-wrap: break-all;
}


.output .previous-operand{
    color: rgba(255,255,255,0.75);
    font-size:1.5vw;

}

.output .current-operand{
    color: white;
    font-size:2.5vw;

}